  border: none;
  border-radius: 2rem;
  font-weight: 700;
  display: none; }
  @media (min-width: 962px) {
    .boton {
      display: block; }
      .boton:hover {
        opacity: 0.7; } }

/** UI **/
.no-webp .principal {
