        text-align: center; }
        .navegacion li:last-of-type {
          margin-right: 0; } }
    .navegacion li:hover {
      border-bottom: 3px solid #001B85; }

hr {
  padding-bottom: 0; }

/*Menú de Hamburgues*/
.mobile-menu {
  padding-top: 2rem; }
  @media (min-width: 962px) {
    .mobile-menu {
      display: none; } }
  .mobile-menu img {
    width: 5rem;
    cursor: pointer; }

/*Side-Bar*/
@media (max-width: 962px) {
  .navegacion {
    display: none;
    position: absolute;
    top: 7.8rem;
    right: 0;
    background-color: #FFFFFF;
    width: 50%;
    height: 100%;
    transition: transform 0.5s ease-in-out; }
  .mostrar {
    display: block; } }

